.main-page-container{
    display: flex;
    flex-direction: column;
    width:100%;
    /* border:4px solid red; */
    box-sizing: border-box;
}
.main-page-section{
    width:100%;
    min-height:700px;
    padding:40px 120px;
    /* border:4px solid blue; */
    box-sizing: border-box;
display: flex;
justify-content: center;
align-items: center;
font-size:48px;
}
@keyframes fadeOut {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
.main-page-banner-container{
    width:1350px;
    margin:0px auto;
    /* padding:0px 285px; */
    box-sizing: border-box;
    border-bottom:1px solid lightgray;
    animation: fadeOut 1s  ease-in-out;
}

.main-page-banner1{
    width:100%;
    height:367px;
    background-image: url(../../asset/image/banner-1.jpeg);
background-repeat: no-repeat;
background-position: center;
}
.main-page-banner2{
    margin-bottom:40px;
    width:100%;
    height:367px;
    background-image: url(../../asset/image/banner-2.jpeg);
background-repeat: no-repeat;
background-position: center;
}
.main-page-banner-customer{
    width: 100%;
    padding:0px 285px;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
}